import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title="All posts" />
        <Bio />
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          return (
            <article key={node.fields.slug}>
              <link href="css/reset.css" rel="stylesheet" type="text/css"></link>
              <link href="css/style.css" rel="stylesheet" type="text/css"></link>
              <link href="css/tiny-slider.css" rel="stylesheet" type="text/css"></link>
              <header>
                <h3
                  style={{
                    marginBottom: rhythm(1 / 4),
                  }}
                >
                  <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
                    {title}
                  </Link>
                </h3>
                <small>{node.frontmatter.date}</small>
              </header>
              <section>
                <p
                  dangerouslySetInnerHTML={{
                    __html: node.frontmatter.description || node.excerpt,
                  }}
                />
              </section>
              <body>
              <div id="contents">
              	<div id="sub">
                  <header>
                      <div class="nav-area">
                          <input class="nav-checkbox" id="nav-input" type="checkbox"></input>
                          <label class="nav-icon" for="nav-input"><span></span></label>
                          <label class="nav-overlay" for="nav-input"></label>
                          <nav class="nav-menu">
                              <ul>
                                  <select name="select" onchange="location.href=value;">
                                      <option value="">--選択--</option>
                                      <option value="#">テスト</option>
                                      <option value="http://monocram-lp.sakura.ne.jp/new_esa/?p=496">説明wiki</option>
                                  </select>
                              </ul>
                          </nav>
                      </div>
                  </header>
                  <div>
              		  <p class="summary">ページリンク</p>
                  </div>
                </div>
                              
              	<div id="main">
                  <div class="my-slider">
                    <div>1</div>
                    <div>2</div>
                    <div>3</div>
                  </div>
                  <div class="customize-tools">
                      <ul class="controls" id="customize-controls">
                        <li>前</li>
                        <li>次</li>
                      </ul>
                      <ul class="thumbnails" id="customize-thumbnails">
                        <li></li>
                        <li></li>
                        <li></li>
                      </ul>
                  </div>
                              
                              
                </div>
                              
              </div>
                              
              <script src="./js/tiny-slider.js"></script>
                  
              </body>
            </article>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
